@import "@_Variable";
@import "@_media";


@include  responsive_min(ssmd) {

  .smallDevices {
    display: none !important;
  }

  .largeDevices {
    display: inline-block;
  }
}

@include responsive_max(md) {

  .home {
    .mainContainer {

      .positionLeft {
        width: 40vw;
      }
    }
  }

  .formContainer {
    .formGroup {
      width: 90% !important;
    }
  }
}

@include responsive_max(smd) {

  .project {

    .mainContainer {
      width: 95%;

      .paginationWrapper {
        width: 60%;
      }

      .projectContent {
        grid-template-columns: repeat(9, 1fr);
        grid-template-areas:
              "left left right right right right right right right"
              "bottom bottom bottom bottom bottom bottom bottom bottom bottom";
        background: rgba(247, 247, 247, .1);
        border-color: $border_color;

        .contentLeft {
          grid-area: left;
        }
        .contentMiddle {
          grid-area: right;

          .projectDate {
            padding: 15px 0;
            position: static;
          }
        }
        .contentRight {
          width: 80%;
          grid-area: bottom;
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          grid-gap: .3em;
          justify-content: center;
          margin: 0 auto;

          a {
            border-color: $border_color;

            &:hover {
              border: 1px solid $white_light_color;
              background: $white_light_color;
            }
          }
        }
      }
    }
  }

  .login {

    .positionLeft {
      .content {
        width: 80% !important;
      }
    }
  }
}

@include responsive_max(ssmd) {

  .main {

    .positionLeft {
      height: fit-content;
      padding-bottom: 10px;

      .content {
        margin-top: 3em;
      }
    }

    .positionRight {

      .content {
        .form {
          width: 95% !important;
        }
      }
    }
  }

}

@include responsive_max(s) {

  .dashboard {
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas: "sidebar sidebar sidebar"
                          "mainContent mainContent mainContent";
  }

  .project {

    .mainContainer {
      width: 100%;
      height: 70%;
      padding: 0 15px;

      .contentButtonContainerPlus {
        border-color: #F5F5F5;
        height: fit-content;
        padding: 0;

        svg {
          font-size: 2.5em;
        }

        span {
          font-size: .8em;
        }
      }

      .contentPaginate {
        overflow: scroll;

        .paginateDataWrapper {
          overflow: hidden;
        }
      }


      .projectContent {

        .contentRight {
          width: 100%;
          grid-gap: .2em;

          * {
            font-size: 1em;
          }
          a {
            padding-left: 2em;

            svg {
              font-size: 1.2em;
            }
          }

          .largeDevices {
            display: none;
          }

          .smallDevices {
            display: inline-block;
          }
        }
      }
      .paginationWrapper {
        width: 90%;
      }
    }
  }

  .dashboard {

    .logoContainer {
      border: none !important;
    }

    .sidebar {

      .sidebarNav {

        .largeDevices {
          display: none;
        }

        .smallDevices {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          background: #161A23;

          .dropdown {
            width: 100%;
            place-items: end;
            position: relative;
            display: inline-block;
            background: inherit;


            .icons, .dropdownContent {
              width: fit-content;
              float: right;
              background: inherit;
            }

            .icons {
              cursor: default;
              width: 20%;
            }

            svg {
              font-size: 1.5em;
              cursor: pointer;
            }

            .dropdownContent {
              border: none;
              position: absolute;
              right: 0;
              margin-top: 2.5em;
              width: 60%;
              padding: 10px 0;


               ul {
                 grid-gap: 0;
                 width: 100%;
                 text-align: center;

                 li {
                   width: 100%;
                   padding-left: 15px;

                   &:hover {
                     background: $white_light_color;
                      a {
                        color: #000;
                      }
                   }
                 }
               }
            }
          }

          .closeMenu, .closeMenu, .dropdownContent {
            display: none;
            opacity: 0;
          }

          .openMenu[open] {
            display: block;
            animation: menu_open .2s ease .5s forwards;
          }
          .closeMenu[open] {
            display: block;
            animation: menu_open .2s ease .5s forwards;
          }
          .dropdownContent[open] {
            display: block;
            animation: dropdown_open .2s ease .5s forwards;
          }
          .openMenu[closing] {
            display: block;
            animation: menu_closing .2s ease .3s forwards;
          }
          .closeMenu[closing] {
            display: block;
            animation: menu_closing .2s ease .3s forwards;
          }
          .dropdownContent[closing] {
            display: block;
            animation: dropdown_closing .2s ease .5s forwards;
          }
          .openMenu[closed] {
            display: none;
          }
          .closeMenu[closed] {
            display: none;
          }
          .dropdownContent[closed] {
            display: none;
          }
        }
      }
    }
  }


  //Goble css style for small devices
  .contentButtonContainerPlus {
    padding: 1em 15px 10px;
    li {
      svg {
        font-size: 40px;
      }
    }
  }

  .taskDueContainer {

    .titleContainer {

      h5 {
        font-size: 1.2em !important;
      }
      i {
        font-size: 1.2em !important;
      }
    }
  }

  .formContainer {
    width: 80%;

    .formGroup {
      width: 100%;
    }
  }
}



@include responsive_max(xs) {

  .appHeader {

    a, li {
      margin-left: .6em;
      font-size: 1.1em;
      cursor: pointer;
    }
  }

  .paginationWrapper {
    width:80%;
    border-radius: 20px;
    padding: 5px 10px;
  }

  .home {
    .mainContainer {
      grid-template-columns: repeat(1, 1fr) !important;
      top: 35%;
      left: 50%;
      transform: translate(-50%, -50%);

      .show_top {
        width: 100%;
        grid-template-columns: 9fr 2fr !important;
      }
      .positionLeft {
        width: 100%;

        nav {
          li {
            text-align: left !important;
          }
        }

        .titleContainer {
           h1 {
             span {
               color: #7F8487;
             }
           }
        }
      }
    }
  }

  .project {
      .paginationWrapper {
        width: 90%;
      }
  }

  .projectBoard {


    .taskContainer {

      nav {
        display: none !important;
      }

      .task {
        grid-template-columns: repeat(1, 1fr) !important;
      }
    }
  }

  .dashboard {

    .mainContent {

      .contentButtonContainerPlus {
        width: 95%;
      }
      .taskDueContainer {
        width: 95%;
      }
    }
  }

  .login {

    .main {
      grid-template-columns: repeat(1, 1fr);
      min-height: 100% !important;

      .positionLeft, .positionRight {
        height: 100%;
      }

      .positionLeft {
        height: fit-content;
        padding-bottom: 10px;

        .content {
          margin-top: 3em;
        }
      }

      .positionRight {
        padding-top: 2.5em;
      }
    }
  }

  .formContainer {
    width: 100% !important;
  }
}
